import axios from 'axios'
import store from '@/store'
import Vue from 'vue'
import ToastPlugin from 'vux/src/plugins/toast/index.js'

Vue.use(ToastPlugin )

axios.defaults.baseURL = 'http://192.168.200.77:8080/CDCT_RCH_CI/http/do';

axios.defaults.timeout = 30000;



axios.interceptors.request.use(config=> {
store.state.CONTROL_LOADDING = true;

if (config.method === 'post' || config.method === 'put' || config.method === 'delete') {

}
return config;
}, error=> {
Vue.$vux.toast.show({
type: 'warn',
text: error
});
return Promise.resolve(error);
})





axios.interceptors.response.use(res => {


store.state.CONTROL_LOADDING = false;
if(typeof res.data != 'undefined' && typeof res.data.code != 'undefined') {
if(res.data.code == '200' && res.data.data != 'undefined') {
return res.data.data;
}else{
Vue.$vux.toast.show({
type: 'warn',
text: res.data.msg
});
}

}else{
Vue.$vux.toast.show({
type: 'warn',
text: ''
});
}

}, error=> {
store.state.CONTROL_LOADDING = false;

if (error.code === 'ECONNABORTED' && error.message.indexOf('timeout') !== -1) {
Vue.$vux.toast.show({
text: '',
time: 2000,
type: 'warn'
});

}
if (error && error.response) {
switch (error.response.status) {
case 400:
error.message = ''
break

case 401:
error.message = ''

break

case 403:
error.message = ''
break

case 404:

error.message = ''
break

case 500:
error.message = ''
break

case 501:
error.message = ''
break

case 502:
error.message = ''
break

case 503:
error.message = ''
break

case 504:
error.message = ''
break

case 505:
error.message = 'HTTP'
break

default:
}
Vue.$vux.toast.show({
text: error.message,
time: 2000,
type: 'warn'
});

}

return Promise.resolve(error);
})

Vue.prototype.$ajax = axios





