const setUp = () => import('@/views/mine/setUp.vue').then(m => m.default)
const safecenter = () => import('@/views/mine/safecenter.vue').then(m => m.default)
const changephonenumber = () => import('@/views/mine/changephonenumber.vue').then(m => m.default)
const changephonenumberVerify = () => import('@/views/mine/changephonenumberVerify.vue').then(m => m.default)
const myAppointment = () => import('@/views/mine/myAppointment.vue').then(m => m.default)
const helpAndFeedback = () => import('@/views/mine/helpAndFeedback.vue').then(m => m.default)
const helpDetails = () => import('@/views/mine/helpDetails.vue').then(m => m.default)
const visitCard = () => import('@/views/mine/visitCard.vue').then(m => m.default)
const PatientManage = () => import('@/views/mine/PatientManage.vue').then(m => m.default)
const visitCardDetails = () => import('@/views/mine/visitCardDetails.vue').then(m => m.default)
const addMedicalCard = () => import('@/views/mine/addMedicalCard.vue').then(m => m.default)
const appointmentDetails = () => import('@/views/mine/appointmentDetails.vue').then(m => m.default)
const EditPatient = () => import('@/views/mine/EditPatient.vue').then(m => m.default)
const serAgreeList = () => import('@/views/mine/serAgreeList.vue').then(m => m.default)
const serAgreeDetail = () => import('@/views/mine/serAgreeDetail.vue').then(m => m.default)

export default [
    {  // 
        path: '/setUp',
        name: 'setUp',
        component: setUp
    },
    {  // 
        path: '/safecenter',
        name: 'safecenter',
        component: safecenter
    },
    {  // 
        path: '/changephonenumber',
        name: 'changephonenumber',
        component: changephonenumber
    },
    {  // 
        path: '/changephonenumberVerify',
        name: 'changephonenumberVerify',
        component: changephonenumberVerify
    },
    {  // 
        path: '/myAppointment',
        name: 'myAppointment',
        component: myAppointment
    },
    {  // 
        path: '/helpAndFeedback',
        name: 'helpAndFeedback',
        component: helpAndFeedback
    },
    {  // 
        path: '/helpDetails',
        name: 'helpDetails',
        component: helpDetails
    },
    {  // 
        path: '/visitCard',
        name: 'visitCard',
        component: visitCard
    },
    {  // 
        path: '/visitCardDetails',
        name: 'visitCardDetails',
        component: visitCardDetails
    },
    {  // 
        path: '/addMedicalCard',
        name: 'addMedicalCard',
        component: addMedicalCard
    },

    {  // 
        path: '/PatientManage',
        name: 'PatientManage',
        component: PatientManage
    },
    {  // 
        path: '/EditPatient',
        name: 'EditPatient',
        component: EditPatient,
    },
    {   // 
        path: '/appointmentDetails',
        name: 'appointmentDetails',
        component: appointmentDetails
    },
    {
        // 
        path: '/serAgreeList',
        name: 'serAgreeList',
        component: serAgreeList
    },
    {
        // 
        path: '/serAgreeDetail',
        name: 'serAgreeDetail',
        component: serAgreeDetail
    }
]


