const IntelligenceGuidance = () => import('@/views/visit/IntelligenceGuidance.vue').then(m => m.default)
const Register = () => import('@/views/visit/register.vue').then(m => m.default)
const symptomDetails = () => import('@/views/visit/symptomDetails.vue').then(m => m.default)
const HospitalList = () => import('@/views/visit/hospitalList.vue').then(m => m.default)
const HospitalInfo = () => import('@/views/visit/hospitalInfo.vue').then(m => m.default)
const HospitalInfo1 = () => import('@/views/visit/hospitalInfo1.vue').then(m => m.default)
const doctorNav = () => import('@/views/visit/doctorNav.vue').then(m => m.default)
const doctorList = () => import('@/views/visit/doctorList.vue').then(m => m.default)
const doctorDetails = () => import('@/views/visit/doctorDetails.vue').then(m => m.default)
const doctorAppointment = () => import('@/views/visit/doctorAppointment.vue').then(m => m.default)


export default [
    {  // 
        path: '/intelligenceGuidance',
        name: 'intelligenceGuidance',
        component: IntelligenceGuidance
    },
    {   // 
        path: '/symptomDetails',
        name: 'symptomDetails',
        component: symptomDetails
      },
    {   // 
        path: '/register',
        name: 'register',
        component: Register
    },
    {   // 
        path: '/doctorNav',
        name: 'doctorNav',
        component: doctorNav
    },
    {   // 
        path: '/doctorList',
        name: 'doctorList',
        component: doctorList
    },
    {   // 
        path: '/doctorDetails',
        name: 'doctorDetails',
        component: doctorDetails
    },
    {   // 
        path: '/hospitalList',
        name: 'hospitalList',
        component: HospitalList
    },
    {   // 
        path: '/hospitalInfo',
        name: 'hospitalInfo',
        component: HospitalInfo
    },
    {    // 2
        path: '/hospitalInfo1',
        name: 'hospitalInfo1',
        component: HospitalInfo1
    },
    {   // 
        path: '/doctorAppointment',
        name: 'doctorAppointment',
        component: doctorAppointment,
        meta: {
            keepAlive: true
        }
    }
]


