
import BScroll from 'better-scroll'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

export default {
components:{InlineLoading},
props: {
isloading: {	//
type: Boolean,
default: false
},
bounce: {	// true 
type: Boolean,
default: true
},

padTop: {	// 10px
type: Boolean,
default: true
},
scrollHeight:{	//100%
type:Boolean,
default:false
},
probeType: {
/**
* ,0,123
* 1.scroll 
* 2. scroll 
* 3. momentum  scroll 
*/
type: Number,
default: 1
},
click: { //click
type: Boolean,
default: true
},
scrollX: {  // true , eventPassthrough  'horizontal' 
type: Boolean,
default: false
},
listenScroll: {	 //
type: Boolean,
default: false
},
data: { // 
type: Array,
default: null
},
pullUpLoad: {
/**
*  false
*  true  Object 
* threshold
*  finishPullUp 
*/
type: [Object,Boolean],
default: function(){
return {
threshold: -50,
stop: 50
}
}

},
pullDownRefresh: {  // 
/**
*  false
*  true  Object 
* threshold stop
*  finishPullDown 
*/
type: [Boolean,Object],
default: false
},
refreshDelay: {
type: Number,
default: 20
},
stopPropagation: {
type: Boolean,
default: false
},
},
mounted(){


this.$nextTick(() => {
this._initScroll();
});

},
methods: {
_initScroll() {

if (!this.$refs.wrapper) {
return;
}
var u = navigator.userAgent;
var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; 
var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); 
let clickType;
if(isAndroid){
clickType = true;
}else{
clickType = false;
}

this.scroll = new BScroll(this.$refs.wrapper, {
probeType: this.probeType,
click: clickType,
scrollX: this.scrollX,
pullUpLoad: this.pullUpLoad,
pullDownRefresh: this.pullDownRefresh,
bounce: this.bounce,
stopPropagation: this.stopPropagation,
})


if (this.listenScroll) {
let THIS = this
this.scroll.on('scroll', (pos) => {
THIS.$emit('scroll', pos)
})
this.scroll.on('scrollEnd', (pos) => {
THIS.$emit('scrollEnd', pos,this.scroll.maxScrollY)
})
}


if (this.pullUpLoad) {
this.scroll.on('pullingUp', () => {
this.$emit('pullingUp')
})
}

if (this.pullDownRefresh) {
this.scroll.on('pullingDown', () => {

this.$emit('pullingDown')
})
}


},
finishPullUp(){
/**
* better-scrollfinishPullUp
*  better-scroll 
*/
this.scroll && this.scroll.finishPullUp()
},
finishPullDown(){
/**
* better-scrollfinishPullDown
*  better-scroll 
*/
this.scroll && this.scroll.finishPullDown()
},

disable() {	// better-scrollDOM  touchstarttouchmovetouchend

this.scroll && this.scroll.disable()
},
enable() {	// better-scroll,  

this.scroll && this.scroll.enable()
},
refresh() {

this.scroll && this.scroll.refresh()
},
goToPage(x, y, time, easing) {

this.scroll && this.scroll.goToPage(x, y, time, easing)
},

scrollTo() {
/**
* better-scrollscrollTo 
* Number} x  px
* {Number} y  px
* {Number} time  ms
* {Object} easing  ease.js 
*/
this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
},
scrollToElement() {
/**
* better-scrollscrollToElement 
* scrollToElement(el, time, offsetX, offsetY, easing)
* {DOM | String} el ,  querySelector  DOM 
* {Number} time  ms
* {Number | Boolean} offsetX  true
* {Number | Boolean} offsetY  true
* {Object} easing  ease.js 
*
*/
this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
},

},
watch: {

data() {
setTimeout(() => {
this.refresh()
}, this.refreshDelay)
},
isloading() {
setTimeout(() => {
this.refresh()
}, this.refreshDelay)
}
}
}



