Vue.mixin({
methods:{
addLinkBg(e){
this.BgTouchY = e.targetTouches[0].pageY;
let bgColor = '#e5e5e5';
const path = e.path || (e.composedPath && e.composedPath());
path.forEach(el => {
if(el.className && el.className.includes('cdct_addLinkitem')){
el.style.transition = 'background-color .5s ease .3s';
el.style.backgroundColor = bgColor;
this._currentElement = el;
return;
}
});
},
moveLinkBg(e){
if(!this._currentElement)return;
let moveY = e.targetTouches[0].pageY;
if(Math.abs(moveY-this.BgTouchY)>20){
this._currentElement.style.transition = 'background-color 0 ease 0';
this._currentElement.style.backgroundColor = '#fff';
}
},
removeLinkBg(e){
if(!this._currentElement)return;
this._currentElement.style.transition = 'background-color 0 ease 0';
this._currentElement.style.backgroundColor = '#fff';
}
}
})


