
import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);

const state = {
    isLoading:false,
    Fileloading:1,  // 1 2 3 4
    CONTROL_LOADDING: false,

}

const getters = {

}

const mutations = {
    updateLoadingStatus(state,payload){
        state.isLoading = payload.isLoading;
    },
    updateFileloadingStatus(state,payload){
        state.Fileloading = payload.Fileloading;
    },


}

const actions = {

}

export default new Vuex.Store({
    state,
    getters,
    mutations,
    actions
})


