import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/views/index'
import HomePage from './homepage.js'
import HealthRrecords from './HealthRrecords.js'
import visit from './visit.js'
import drugsAndElse from './drugsAndElse.js'
import mine from './mine.js'
import store from '../store'
const Home = () => import('@/views/HomePage/home.vue').then(m => m.default)
const Kep = () => import('@/views/HomePage/kep.vue').then(m => m.default)
const Mine = () => import('@/views/HomePage/mine.vue').then(m => m.default)
const Visit = () => import('@/views/HomePage/visit.vue').then(m => m.default)
const Testpage1 = () => import('@/views/testpage/testpage1.vue').then(m => m.default)
const WaitUs = () => import('@/views/polularScience/waitUs.vue').then(m => m.default)
const WaitUs2 = () => import('@/views/polularScience/waitUs2.vue').then(m => m.default)
const WaitUs3 = () => import('@/views/polularScience/waitUs3.vue').then(m => m.default)
const WaitUs4 = () => import('@/views/polularScience/waitUs4.vue').then(m => m.default)
const search = () => import('@/views/HealthRrecords/search.vue').then(m => m.default)
const searchNext = () => import('@/views/HealthRrecords/searchNext.vue').then(m => m.default)

Vue.use(Router)
Router.prototype.goBack = function () { 
  this.isBack = true
  window.history.go(-1)
}
const router = new Router({
  routes: [
    {
      path:'/',
      name:'index',
      component: Index,
      redirect:'/home',
      children:[
        {
          path:'home',
          name:'home',
          component:Home,
        },
        {
          path:'kep',
          name:'kep',
          component: WaitUs
        },
        {
          path:'mine',
          name:'mine',
          component:Mine
        },
        {
          path:'visit',
          name:'visit',
          component:Visit
        },
      ]
    },
	  {
      path:'/testpage1',
      name:'testpage1',
      component:Testpage1
    },
    {
      path: '/waitUs2',
      name: 'waitUs2',
      component: WaitUs2
    },
    {
      path: '/waitUs3',
      name: 'waitUs3',
      component: WaitUs3
    },
    {
      path: '/waitUs4',
      name: 'waitUs4',
      component: WaitUs4
    },
    {
      path: '/search',
      name: 'search',
      component: search
    },
    {
      path: '/searchNext',
      name: 'searchNext',
      component: searchNext
    },
    ... HealthRrecords,
    ...visit,
    ...mine,
    ...drugsAndElse
  ],
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})
