  // if(from.name == 'home' && to.name == 'FileLogin'){
  //   if(store.state.Fileloading == 1){
  //     next('/RealNameSystem')
  //   }else if(store.state.Fileloading == 3){
  //     next('/AssociatedFiles')
  //   }else if(store.state.Fileloading == 4){
  //     next()
  //   }
    
    
  // }
  // store.commit('updateLoadingStatus', {
  //   isLoading: true
  // });
  // next();
// })
// router.afterEach((to) => {
  // setTimeout(() => {
  //   store.commit('updateLoadingStatus', {
  //     isLoading: false
  //   });
  // }, 500)
// })

export default router


