//
import './vripple.css';
//Ripple
export const Ripple = {
    install(Vue,option){
        Vue.directive('ripple', {
            //
            bind(el,binding) {
                //class,canvas
                let canvas = document.createElement('canvas');
                el.classList.add('vripple-wrapper');
                canvas.classList.add('vripple-circle');
                el.appendChild(canvas);                
            },

            //  DOM 
            inserted(el, binding, vnode, oldVnode) {

                let value = binding.value||{} //
                let color = value.color||'#fff';//color#fff              
                let height = el.offsetHeight;//
                let width  = el.offsetWidth;//
                let top = el.offsetTop;//
                let left = el.offsetLeft;//
                let canvas = el.querySelector('.vripple-circle');
                    canvas.height = height;
                    canvas.width = width;
                var ctx=canvas.getContext("2d");
                let timer;
                let startY;
                let timeout;
                el.addEventListener('touchstart',(e)=>{ 
                    let startY = e.targetTouches[0].pageY;
                    canvas.style.display = 'block';
                    clearInterval(timer);// setInterval
                    let tar = e.changedTouches[0];//touch
                    let x = tar.pageX-left;//touchx
                    let y = tar.pageY-top;//touchy
                    let scale = 5;//
                    let radius = 2;//     
                    let max = height>width?height:width;//
                    let opcity = value.opcity||0.5;//0.5
                    //
                    timeout = setTimeout(()=>{
                        timer = setInterval(()=>{
                            opcity *= 0.96;//
                            radius += scale;//
                            ctx.clearRect(0,0,width,height);
                            ctx.beginPath();
                            ctx.globalAlpha = opcity;
                            ctx.arc(x,y,radius,0,2*Math.PI);
                            ctx.fillStyle = color;
                            ctx.fill();
                            //-40
                            if(radius-40>max){
                                clearInterval(timer);
                                ctx.clearRect(0,0,width,height);
                            }
                        },1000/60) 
                    },300)
                    

                },false);
                el.addEventListener('touchmove',e=>{
                    let moveY = e.targetTouches[0].pageY;
                    clearTimeout(timeout);
                    if(Math.abs(moveY-startY)>20){
                        canvas.style.display = 'none';
                    }
                })
                // el.addEventListener('touchend',e=>{
                //     console.log('111');
                //     ctx.clearRect(0,0,width,height);
                // })
            },
            // VNode 
            update() {},
             // VNode VNode
            componentUpdated() {},
            //
            unbind(el) {
                let canvas = el.querySelector('.vripple-circle');
                el.removeChild(canvas);
            }
        });
    }
}
 


