    },
    methods:{
        sendData(){
             var u = navigator.userAgent;
             var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android
             var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios

             if(this.$root.loginInfo.isLogin==true){
                 var login = localStorage.getItem('loginInfo');
             }else{
                 if(isAndroid){
                     android:AndroidFun.mutual('1');
                 } 
                 if(isiOS){
                     window.webkit.messageHandlers.mutual.postMessage({type: "1"});
                 }

             }
         }
    },
    mounted() {
        
    },

}



