const drugs = () => import('@/views/drugsAndElse/drugs.vue').then(m => m.default)
const drugsList = () => import('@/views/drugsAndElse/drugsList.vue').then(m => m.default)
const drugsDetails = () => import('@/views/drugsAndElse/drugsDetails.vue').then(m => m.default)
const IndexInterpretation = () => import('@/views/drugsAndElse/IndexInterpretation.vue').then(m => m.default)
const rangeList = () => import('@/views/drugsAndElse/rangeList.vue').then(m => m.default)
const rangeListSearch = () => import('@/views/drugsAndElse/rangeListSearch.vue').then(m => m.default)
const diseaseListSearch = () => import('@/views/drugsAndElse/diseaseListSearch.vue').then(m => m.default)
const drugsSearch = () => import('@/views/drugsAndElse/drugsSearch.vue').then(m => m.default)
const disease = () => import('@/views/drugsAndElse/disease.vue').then(m => m.default)
const diseaseList = () => import('@/views/drugsAndElse/diseaseList.vue').then(m => m.default)
const symptoms = () => import('@/views/drugsAndElse/symptoms.vue').then(m => m.default)
const targetdetail = () => import('@/views/drugsAndElse/targetdetail.vue').then(m => m.default)

export default [
    {  // 
        path: '/drugs',
        name: 'drugs',
        component: drugs
    },
    {  // 
        path: '/drugsList',
        name: 'drugsList',
        component: drugsList
    },
    {  // 
        path: '/drugsSearch',
        name: 'drugsSearch',
        component: drugsSearch
    },
    {  // 
        path: '/drugsDetails',
        name: 'drugsDetails',
        component: drugsDetails
    },
    {  // 
        path: '/IndexInterpretation',
        name: 'IndexInterpretation',
        component: IndexInterpretation
    },
    {  // 
        path: '/rangeList',
        name: 'rangeList',
        component: rangeList
    },
    {  // 
        path: '/rangeListSearch',
        name: 'rangeListSearch',
        component: rangeListSearch
    },
    {  // 
        path: '/targetdetail',
        name: 'targetdetail',
        component: targetdetail
    },
    {  // 
        path: '/disease',
        name: 'disease',
        component: disease
    },
    {  // 
        path: '/diseaseList',
        name: 'diseaseList',
        component: diseaseList
    },
    {  // 
        path: '/diseaseListSearch',
        name: 'diseaseListSearch',
        component: diseaseListSearch
    },
    {  // 
        path: '/symptoms',
        name: 'symptoms',
        component: symptoms
    }
]


